/* style for TopNav */
.topNav {
	background-color: rgb(143, 32, 134);
	/* background-color: #fff; */
	height: 50px;
	border: none;
}

.textTopNavLeft {
	font-size: 14px;
	font-family: "Ubuntu";
	color: #fff;
}
.textTopNavRight {
	font-size: 14px;
	font-family: "Ubuntu";
	color: rgba(255, 255, 255, 0.781);
}
.telefonTopNav {
	padding: 5px 35px;
	border: 1px solid #fff;
	border-radius: 100px;
	color: #fff !important;
}
.telefonTopNav:hover {
	text-decoration: none;
}

.padd-15px-right {
	padding-right: 15px;
}
